export default {
  props:["infoId"],
  data() {
    return {
      warning: 10,
      info:{"pt":{}},
      title:{
            name:"messageinfo",
            flag:false,
			backUrl:"/warning"
	  },
	  isAndroid:false,
	  defaultImg:"this.src='"+require('../../assets/images/1108101904.png')+"'",
	  isReload:true,
    };
  },
	mounted:function () {
		window.setI18nValue = this.$setI18nValue;//国际化赋值
		window.goBack=this.goBack;
		this.requestAjax();
	},
  created() {
		let _this=this;
  	this.isAndroid=this.$isAndroid();
	console.log("info ing infoId==>"+this.infoId)
     //qurl="http://10.3.10.153:7002/phone/getOneMessages"
  },
  methods: {
		requestAjax(){
			var  _this=this;
			var infoId=this.$route.query.infoId;
			var infotype=this.$route.query.type;
			if(infotype){
				_this.title.backUrl="/focus?jump=false"
			}
			if(!infoId){
				_this.isReload=false;
				console.log("open detail==>"+this.infoId)
				infoId=this.infoId;
			}
			_this.$vux.loading.show({text: _this.$t('loading')})//打開正在加載
			var i18nStr=window.localStorage.getItem('i18nStr');
			if(i18nStr==undefined){
				console.log("i18nStr is null ")
				_this.$i18n.locale = "zh-CN";//关键语句
			}else{
				console.log("i18nStr is not null ")
				_this.$i18n.locale =i18nStr;//关键语句
			}
			//this.info=JSON.parse(infoObj);
			console.log(infoId);
			let method = 'POST';
			var qurl=this.$myConfig.ip+":"+this.$myConfig.port+"phone/getOneMessages";
			let testUrl = window.g.test_getOneMessages;
			if(testUrl) {
				qurl=testUrl;
				method='GET';
			}
			$.ajax({
					type: method,
					url: qurl, 
					data:{key:this.$myConfig.key,messageId:infoId},//All 24小是发布过的||NOW 24小时正在发布的
					//url: "http://192.168.91.84:8080/phone/weatherforecast",
					//data: "areaId=" + cityCode + "&dataType="+qtype,
					dataType: "json",
					async: true,
					success: function(data) {
						console.log(data);
						if(data.success){
							_this.info=JSON.parse(data.message);
							//console.log(_this.info.warnname)
							//_this.changeInfoContent(_this.$i18n.locale)
							console.log(_this.info);
						}else{
							_this.showBottomToast("預警詳情接口異常")
						}
						_this.$vux.loading.hide();//關閉正在加載
					},error:function(){
						_this.$vux.loading.hide();//關閉正在加載
					}
				});	
		},
		changeInfoContent(i18nStr){
			var _this=this;
			if(i18nStr=='en-US'){
				if(_this.info.en.warnname==''){
					console.log("en warn is null")
					_this.info.en.warnname=_this.info.warnname;
				}
				if(_this.info.en.warncontent==''){
					console.log("en warncontent is null")
					_this.info.en.warncontent=_this.info.warncontent;
				}
			}else if(i18nStr=='pt-PT'){
				if(_this.info.pt.warnname==''){
					console.log("pt warn is null")
					_this.info.pt.warnname=_this.info.warnname;
				}
				if(_this.info.pt.warncontent==''){
					console.log("pt warncontent is null")
					_this.info.pt.warncontent=_this.info.warncontent;
				}
			}
		},
    playM(warnid){
    	console.log("play Id is ==>"+warnid)
  		if(this.info.flag){
  			this.info.flag=false;
  			if(this.isAndroid){
	    		this.showBottomToast(this.$t('stopplay'))
	    		//window.android.stopSpeakText();
					window.android.stopSpeakVoice()
	    	}else{
	    		console.log("ios init!")
					window.location.href="stopSpeakText";
	    	}
  		}else{
  			this.info.flag=true;
	    	this.showBottomToast(this.$t('getcontent'))
				var content="";
				var playUrl="";
				content=this.info.warncontent;
				playUrl=this.info.voiceurl;
				/* if(this.$i18n.locale=='zh-CN'){
					content=this.info.warncontent;
					playUrl=this.info.voiceurl;
				}else if(this.$i18n.locale=='pt-PT'){
					content=this.info.pt.warncontent;
					playUrl=this.info.pt.voiceurl;
					if(content==undefined||content==''){
						content=this.info.warncontent;
					}
					if(playUrl==undefined||playUrl==''){
						playUrl=this.info.voiceurl;
					}
				}else{
					content=this.info.en.warncontent;
					playUrl=this.info.en.voiceurl;
					if(content==undefined||content==''){
						content=this.info.warncontent;
					}
					if(playUrl==undefined||playUrl==''){
						playUrl=this.info.voiceurl;
					}
				} */
				
	    	if(this.isAndroid){
	    	//window.android.speakText(content);
			if(playUrl){
				window.android.speakVoiceUrl(playUrl)
			}
	    	}else{
	    		console.log("ios init!")
					var hcontent=encodeURI(content)
					window.location.href="speakText:"+this.$i18n.locale+"@@@"+hcontent;
	    	}
  		}
    	return false;
    },
    showBottomToast(val){
			// 显示
			this.$showBottomToast(val, 'bottom')
	},
	goBack() {
	  if(this.isReload){
		  var backUrl=this.title.backUrl;
		  console.log("param===>come in"+backUrl)
		  console.log("title==>"+backUrl)
		  if(backUrl!=undefined&&backUrl!=null&&backUrl!='null'&&backUrl!=''){
		  	  if(backUrl=="/index"){
		  	  	console.log("param===>come in end")
		  					if(this.$isAndroid()){
		  						window.android.goBack();
		  					}else{
		  						//window.location.href="popOut";
		  					}
		  	  	/*window.android.destory();*/
		  	  	//this.$router.go(-1); //返回上一次历史记录
		  	  	
		  	  }else{
		  	  	 this.$router.push(backUrl);
		  	  }
		  				if(this.$isAndroid()){
		  						window.android.stopSpeakVoice()
		  					}
		  }else{
		  	console.log("param===>not come in")
		  	 this.$router.go(-1); //返回上一次历史记录
		  }
	  }else{
		  this.$emit("closeInfo")
	  }
	  
	},
  }
};



